import React, { useState, useEffect, useContext } from 'react';
import { AdminHeader, AdminSidebar } from 'src/components';
import AllFirms_logo from 'src/assets/images/admin_dashboard_icons/AllFirms.png';
import { Form, FormControl, FormGroup, Row, Col, Button, Modal } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { UserContext } from 'src/context/UserContextProvider';
import 'src/scss/_custom.scss';

const SHOW_USER_DETAILS_API_URL = `http://13.233.194.13:3007/api/users/user_details/`;
const UPDATE_USER_DETAILS_API_URL = `http://13.233.194.13:3007/api/users/update_user_details/`;

const User_Edit_Profile = () => {
  const { user } = useContext(UserContext);
  const userId = user.userId;

  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [contact, setContact] = useState('');
  const [address, setAddress] = useState('');
  const status = "1"; // Hardcoded status
  const role = "1"; // Assuming '2' is the role for the user being added
  const [showModal, setShowModal] = useState(false);
  const [successMessage, setSuccessMessage] = useState('');
  const [errorMessage, setErrorMessage] = useState('');

  // Fetch user details
  useEffect(() => {
    const fetchUserDetails = async () => {
      try {
        const response = await fetch(`${SHOW_USER_DETAILS_API_URL}${userId}`);
        if (response.ok) {
          const data = await response.json();
          const userDetails = data.data;
          setName(userDetails.usr_name);
          setEmail(userDetails.usr_email);
          setPassword(userDetails.password);
          setContact(userDetails.usr_contact);
          setAddress(userDetails.usr_address);
        } else {
          throw new Error('Failed to fetch user details');
        }
      } catch (error) {
        console.error('Error fetching user details:', error);
        setErrorMessage('Failed to fetch user details. Please try again.');
      }
    };

    fetchUserDetails();
  }, [userId]);

  // Update user details
  const handleUpdateUser = async () => {
    try {
      const response = await fetch(`${UPDATE_USER_DETAILS_API_URL}${userId}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          name,
          email,
          password,
          contact,
          address,
          status,
          role
        }),
      });

      if (response.ok) {
        setSuccessMessage('User updated successfully!');
        setErrorMessage('');
        setTimeout(() => {
          window.location.reload();
        }, 1000);
      } else {
        throw new Error('Failed to update user');
      }
    } catch (error) {
      console.error('Error updating user:', error);
      setErrorMessage('Failed to update user. Please try again.');
      setSuccessMessage('');
      setTimeout(() => {
        window.location.reload();
      }, 1000);
    }
  };

  return (
    <div>
      <AdminSidebar />
      <div className="wrapper d-flex flex-column min-vh-100 bg-light">
        <AdminHeader />
        <div className="body flex-grow-1 px-3">
          <>
            <Row className="justify-content-center mb-3">
              <Col xs={8}>
                <div className="card_border_color_change py-5" style={{ borderColor: "white", borderRadius: "10px" }}>
                  <Row className="allfirms_title_mainrow">
                    <Col xs={9} style={{ marginLeft: "10px" }}>
                      <Row className="allfirms_title_row py-1 align-items-center">
                        <Col xs={1}>
                          <img src={AllFirms_logo} width={25} height={25} alt="All Firms Logo" />
                        </Col>
                        <Col xs={4}>
                          <h4>My Profile</h4>
                        </Col>
                      </Row>
                    </Col>
                  </Row>

                  <Row className="allfirms_table_row py-5 justify-content-center">
                    <Col xs={12} md={6}>
                      <Form>
                        <FormGroup className="mb-3" controlId="formName">
                          <FormControl
                            type="text"
                            placeholder="Name"
                            value={name}
                            readOnly
                          />
                        </FormGroup>

                        <FormGroup className="mb-3" controlId="formEmail">
                          <FormControl
                            type="email"
                            placeholder="Email"
                            value={email}
                            readOnly
                          />
                        </FormGroup>

                        <FormGroup className="mb-3" controlId="formPassword">
                          <FormControl
                            type="password"
                            placeholder="Password"
                            value={password}
                            readOnly
                          />
                        </FormGroup>

                        <FormGroup className="mb-3" controlId="formContact">
                          <FormControl
                            type="text"
                            placeholder="Contact"
                            value={contact}
                            readOnly
                          />
                        </FormGroup>

                        <FormGroup className="mb-3" controlId="formAddress">
                          <FormControl
                            type="text"
                            placeholder="Address"
                            value={address}
                            readOnly
                          />
                        </FormGroup>

                        <Row className="justify-content-center align-content-center">
                          <Button onClick={() => setShowModal(true)} className='col-md-3' id='but_color'>Edit</Button>
                        </Row>
                      </Form>
                    </Col>
                  </Row>
                </div>
              </Col>
            </Row>
          </>
        </div>
      </div>

      <Modal show={showModal} onHide={() => setShowModal(false)}>
        <Modal.Header closeButton>
          <Modal.Title>Edit User Details</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            <FormGroup className="mb-3" controlId="formName">
              <FormControl
                type="text"
                placeholder="Name"
                value={name}
                onChange={(e) => setName(e.target.value)}
              />
            </FormGroup>

            <FormGroup className="mb-3" controlId="formEmail">
              <FormControl
                type="email"
                placeholder="Email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
              />
            </FormGroup>

            <FormGroup className="mb-3" controlId="formPassword">
              <FormControl
                type="password"
                placeholder="Password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
              />
            </FormGroup>

            <FormGroup className="mb-3" controlId="formContact">
              <FormControl
                type="text"
                placeholder="Contact"
                value={contact}
                onChange={(e) => setContact(e.target.value)}
              />
            </FormGroup>

            <FormGroup className="mb-3" controlId="formAddress">
              <FormControl
                type="text"
                placeholder="Address"
                value={address}
                onChange={(e) => setAddress(e.target.value)}
              />
            </FormGroup>

            <Row className="justify-content-center align-content-center">
              <Button onClick={handleUpdateUser} className='col-md-3' id='but_color'>Save</Button>
              {errorMessage && <div className="text-danger mt-2 text-center">{errorMessage}</div>}
              {successMessage && <div className="text-success mt-2 text-center">{successMessage}</div>}
            </Row>
          </Form>
        </Modal.Body>
      </Modal>
    </div>
  );
};

export default User_Edit_Profile;
